body {
  background: #FFF;
  line-height: 1.4;
  font-size: $fz;
  min-width: 320px;
  @include reg;
  color: $c-text;
  @include no-scroll; }

input, textarea {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  @include placeholder {
    font-family: inherit;
    font-size: inherit; } }

.icon {
  display: inline-block;
  fill: currentColor;
  font-size: inherit; }

.btn {
  text-transform: uppercase;
  color: $white;
  background-color: $accent;
  display: inline-block;
  min-width: 260px;
  padding: 12px 10px;
  @include bold;
  border-radius: 25px; }
