strong {
  font-weight: 400;
  @include bold; }

small {
  font-size: inherit; }

em, i {
  font-style: normal;
  @include italic; }

h1, h2, h3, h4, h5, h6 {
  font-weight: 400; }

.h1, h1 {
  color: $white;
  @include css-lock(24, 48, 320, 1199, font-size);
  text-shadow: 0px 5px 0px rgba(0, 0, 0, 0.28);
  line-height: 1.2;
  @include bold; }

.h2, h2 {}

.h3, h3 {}

.h4, h4 {}

.h5, h5 {}

.h6, h6 {}

.text {
  &--gray {
    color: $gray; } }
