@include font('montserrat-regular', 'montserrat-regular-webfont');
@include font('montserrat-bold', 'montserrat-bold-webfont');
@include font('montserrat-regularitalic', 'montserrat-regularitalic-webfont');

@mixin reg {
  font-family: 'montserrat-regular', Arial, sans-serif; }
@mixin bold {
  font-family: 'montserrat-bold', Arial, sans-serif; }
@mixin italic {
  font-family: 'montserrat-regularitalic', Arial, sans-serif; }
